.app {
  background-color: rgb(94, 94, 94);
  height: calc(100vh - 70px);
  width: 100vw;
  padding-top: 70px;
}

.results {
  display: flex;
  align-items: center;
  gap: 25px;
  justify-content: center;
  margin-top: 100px;
}

.results--item {
  background-color: black;
  padding: 20px;
  color: white;
  font-size: 24px;
  font-weight: bold;
}

.label {
  margin-right: 30px;
  color: white;
  font-size: 34px;
  font-weight: bold;
}

.entries {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 60px;
  flex-direction: column;
}

.entries--item {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 40px;
}

.entries--item button {
  font-size: 24px;
  padding: 15px;
}